<template>
  <div id="encyclopedias">
    <van-search
      v-model="value"
      placeholder="请输入搜索关键词"
      show-action
      shape="square"
      @search="onSearch"
      @keyup="keyupSearch(value)"
    >
      <div slot="action" @click="onSearch">搜索</div>
    </van-search>
  <ul>
       <li v-for="(item,index) in listarrResult" @click="choose(item.key,item.value)" v-if="show">
         <input type="hidden" v-model="item.key"/>
         {{item.value}}
          <i class="icon iconfont icon-iconfonti"></i>
       </li>
      <li v-for="(item,index) in listarrResult1" @click="choose(item.key,item.value)" v-if="!show">
        <input type="hidden" v-model="item.key"/>
        {{item.value}}
        <i class="icon iconfont icon-iconfonti"></i>
      </li>
   </ul>
 </div>
</template>

<script>
  import { Search } from 'vant';
  export default {
    data(){
      return {
        value:'',
        key: '',
        listarr:[],
        listarrResult:[],
        listarrResult1:[],
        show:true
      }
    },

    methods:{
      keyupSearch(value) {
        this.show = false;
        this.listarrResult1= this.listarrResult.filter(item => item.value.includes(value));


      },
      async onSearch() {
          this.$router.push('/detail?code='+this.key);

       },
      async init() {
        this.listarr = await this.$http.get(this.$HOST + '/open/xk/dict');

        this.listarrResult=this.listarr.result;


      },
      choose(key,value) {
        this.value = value;
        this.key = key;
      }
    },

   created(){
      this.init();
       // this.onSearch();
    }
  }
</script>

<style scoped lang="scss">
  $height80:80px;
  $bg-fff:#ffffff;

 #encyclopedias{
   background:$bg-fff;
   height: auto;
   ul{
     li {
       height: $height80;
       line-height:  $height80;
       color: #666666;
       text-align: left;
       text-indent: 20px;
       border-bottom: 1px solid #99999982;
       font-size: 24px;
       .iconfont{
         float: right;
         padding-right: 50px;
         font-size: 24px;
       }
     }
   }
}
</style>



